/* === base === */
* { box-sizing: border-box; }

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;

  /* Use your custom background (local asset) */
  background: url('./assets/background.png') no-repeat center center fixed;
  background-size: cover;

  /* Fallback color if image hasn't loaded yet */
  background-color: #0f0f13;

  color: #ffffff;

  /* optional: soft fade-in */
  animation: bgfade 1.2s ease-in;
}

@keyframes bgfade {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Keep your appâ€™s content width and spacing */
#root > div { 
  max-width: 1000px; 
  margin: 24px auto; 
  padding: 16px; 
}

/* Glass/overlay for readability (kept from your file) */
#root {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  padding: 20px;
  backdrop-filter: blur(6px);
  /* optional: subtle shadow so the card lifts from bg */
  box-shadow: 0 0 20px rgba(0,0,0,0.25);
}

/* --- typography --- */
h1 { font-size: 28px; margin: 0 0 16px; }
h2 { font-size: 18px; margin: 0 0 8px; }

/* --- form controls --- */
input[type="text"], input:not([type]) {
  width: 260px; 
  padding: 10px 12px; 
  border-radius: 8px;
  border: 1px solid #2b2650; 
  background: #171332; 
  color: #e8e9f1; 
  outline: none;
}
input::placeholder { color: #9aa0b7; }

button {
  padding: 10px 14px; 
  margin-left: 8px; 
  border: none; 
  border-radius: 8px;
  background: #5a4ff3; 
  color: white; 
  cursor: pointer;
}
button:hover { filter: brightness(1.05); }
button:disabled { opacity: 0.6; cursor: not-allowed; }

